import React from "react";
import animatedScrollTo from "animated-scroll-to";
import { isMobile } from "react-device-detect";
import { Container, Row, Col } from "react-bootstrap";
import { CgMediaPodcast } from "react-icons/all";
import Logo from "../addons/images/logo.png";
import { Link } from "react-router-dom";
import { Button } from "../styles/main-theme";

const RenderLogo = () => {
  return (
    <Col lg={12}>
      <img src={Logo} alt="logo.png" style={{ width: "100%" }} />
    </Col>
  );
};

export default function MediaMarketing() {
  React.useEffect(() => {
    animatedScrollTo(0);
  }, []);

  return (
    <>
      <Container style={{ margin: "10% auto", textAlign: "center" }}>
        <Row>
          {isMobile ? <RenderLogo /> : null}
          <Col lg={12}>
            <CgMediaPodcast
              style={{ transform: "scale(3)", margin: "5% auto" }}
            />
          </Col>
        </Row>
        <Row>
          <Col lg={12}>
            <u>Media Marketing</u>
          </Col>
          <Col lg={12} style={{ margin: "5%" }}>
            <h2>
              Social media marketing is the use of social media platforms to
              connect with your audience to build your brand, increase sales,
              and drive website traffic. This involves publishing great content
              on your social media profiles, listening to and engaging your
              followers, analyzing your results, and running social media
              advertisements.
            </h2>
          </Col>
          <Col lg={12}>
            <h2>
              The major social media platforms (at the moment) are Facebook,
              Instagram, Twitter, LinkedIn, Pinterest, YouTube, and Snapchat.
            </h2>
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            <h2>
              There are also a range of social media management tools that help
              businesses to get the most out of the social media platforms
              listed above. For example, Buffer is a platform of social media
              management tools, which can help you achieve success with your
              social media marketing. Whether you want to build a brand or grow
              your business, we want to help you succeed.
            </h2>
          </Col>
          <Col lg={12}>
            <h2>
              Social media marketing first started with publishing. Businesses
              were sharing their content on social media to generate traffic to
              their websites and, hopefully, sales. But social media has matured
              far beyond being just a place to broadcast content.
            </h2>
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            <h2>
              Nowadays, businesses use social media in a myriad of different
              ways. For example, a business that is concerned about what people
              are saying about its brand would monitor social media
              conversations and response to relevant mentions (social media
              listening and engagement). A business that wants to understand how
              it’s performing on social media would analyze its reach,
              engagement, and sales on social media with an analytics tool
              (social media analytics). A business that wants to reach a
              specific set of audience at scale would run highly-targeted social
              media ads (social media advertising).
            </h2>
          </Col>
          <Col lg={3} />
          <Col lg={6}>
            <Link to="/contact-us">
              <Button>Enquire Prices</Button>
            </Link>
          </Col>
        </Row>
      </Container>
    </>
  );
}
