import React from "react";
import animatedScrollTo from "animated-scroll-to";
import { Container, Row, Col } from "react-bootstrap";
import { BiCctv } from "react-icons/all";
import { isMobile } from "react-device-detect";
import Logo from "../addons/images/logo.png";
import { Link } from "react-router-dom";
import { Button } from "../styles/main-theme";

const RenderLogo = () => {
  return (
    <Col lg={12} style={{ margin: "5% auto" }}>
      <img src={Logo} alt="logo.png" style={{ width: "100%" }} />
    </Col>
  );
};

export default function CCTV() {
  React.useEffect(() => {
    animatedScrollTo(0);
  }, []);

  return (
    <>
      <Container style={{ margin: "10% auto", textAlign: "center" }}>
        <Row>
          {isMobile ? <RenderLogo /> : null}
          <Col lg={12}>
            <BiCctv style={{ transform: "scale(3)", margin: "5% auto" }} />
          </Col>
          <Col lg={12}>
            <u>CCTV</u>
          </Col>
        </Row>
        <Row>
          <Col lg={12}>
            <h2>
              When it comes to securing your business, there are many different
              types of CCTV to choose from. Surveillance plays a huge part in
              today’s society, and with cameras all around us, our day-to-day
              lives are experiencing higher levels of security each day.
            </h2>
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            <h2>
              What many people don’t know, however, is that there are a variety
              of different types of CCTV camera which suit different situations
              or premises, and that selecting the proper camera for the right
              application really is vital.
            </h2>
          </Col>
          <Col lg={12}>
            <h2>
              CCTV plays a huge part in today’s society, and with cameras all
              around us, our day-to-day lives are experiencing higher levels of
              security each day. What many people don’t know, however, is that
              there are a variety of different types of CCTV camera which suit
              different situations or premises, and that selecting the proper
              camera for the right application really is vital. Here, we run
              through these types of camera and what makes them unique and more
              suitable for some venues over others.
            </h2>
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            <u>Different Types of CCTV</u>
            <h2>
              Here, we run through these types of camera and what makes them
              unique and more suitable for some venues over others.
            </h2>
          </Col>
          <Col lg={12}>
            <ul>
              <li>
                <h2>Dome Camera</h2>
              </li>
              <li>
                <h2>Bullet Camera</h2>
              </li>
              <li>
                <h2>C-mount Camera</h2>
              </li>
              <li>
                <h2>Day/Night Camera</h2>
              </li>
              <li>
                <h2>PTZ Camera</h2>
              </li>
            </ul>
          </Col>
          <Col lg={3} />
          <Col lg={6}>
            <Link to="/contact-us">
              <Button>Enquire Prices</Button>
            </Link>
          </Col>
        </Row>
      </Container>
    </>
  );
}
