import styled from "styled-components";

export const Button = styled.button`
  width: 100%;
  background-color: transparent;
  border: solid 0.1rem #342b85;
  padding: 0.5rem;
  border-radius: 0.2rem;
  color: black;
  font-weight: 600;

  transition: all 300ms;
  cursor: pointer;
  outline: none !important;

  &:hover {
    background-color: #342b85;
    color: white;
    transform: scale(1.1);
  }

  &:focus {
    box-shadow: 0 0 0 0.2rem rgba(48, 44, 135, 0.5);
  }
`;

export const SidebarButton = styled.button`
  width: 100%;
  background-color: transparent;
  border: none;
  border-bottom: solid 0.2rem #342b85;

  outline: none !important;

  &:focus {
    box-shadow: 0 4px 2px -2px rgba(48, 44, 135, 0.8);
  }
`;

export const Hamburger = styled.button`
  border: none;
  outline: none !important;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 99;

  background-color: transparent;
`;

export const SubmitButton = styled.button`
  border: solid 0.2rem #342b85;
  outline: none !important;
  width: 80%;
  padding: 0.5rem;

  border-radius: 0.25rem;

  background-color: transparent;

  cursor: pointer;

  transition: all 300ms;
  &:hover {
    transform: scale(1.1);
    color: white;
    background-color: #342b85;
  }

  &:focus {
    box-shadow: 0 0 0 0.2rem rgba(48, 44, 135, 0.5);
  }
`;
