import React from "react";
import animatedScrollTo from "animated-scroll-to";
import { Container, Row, Col } from "react-bootstrap";
import { FaNetworkWired } from "react-icons/all";
import Logo from "../addons/images/logo.png";
import { isMobile } from "react-device-detect";
import { Link } from "react-router-dom";
import { Button } from "../styles/main-theme";

const RenderLogo = () => {
  return (
    <Col lg={12}>
      <img src={Logo} alt="logo.png" style={{ width: "100%" }} />
    </Col>
  );
};

export default function Networking() {
  React.useEffect(() => {
    animatedScrollTo(0);
  }, []);

  return (
    <>
      <Container style={{ margin: "10% auto", textAlign: "center" }}>
        <Row>
          {isMobile ? <RenderLogo /> : null}
          <Col lg={12}>
            <FaNetworkWired
              style={{ transform: "scale(3)", margin: "5% auto" }}
            />
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            <u>Networking</u>
          </Col>
        </Row>
        <Row>
          <Col lg={12}>
            <h2>
              Networking, also known as computer networking, is the practice of
              transporting and exchanging data between nodes over a shared
              medium in an information system. Networking comprises not only the
              design, construction and use of a network, but also the
              management, maintenance and operation of the network
              infrastructure, software and policies.
            </h2>
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            <h2>
              Computer networking enables devices and endpoints to be connected
              to each other on a local area network (LAN) or to a larger
              network, such as the internet or a private wide area network
              (WAN). This is an essential function for service providers,
              businesses and consumers worldwide to share resources, use or
              offer services, and communicate. Networking facilitates everything
              from telephone calls to text messaging to streaming video to the
              internet of things (IoT).
            </h2>
          </Col>
          <Col lg={12}>
            <h2>
              The level of skill required to operate a network directly
              correlates to the complexity of a given network. For example, a
              large enterprise may have thousands of nodes and rigorous security
              requirements, such as end-to-end encryption, requiring specialized
              network administrators to oversee the network.
            </h2>
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            <h2>
              At the other end of the spectrum, a layperson may set up and
              perform basic troubleshooting for a home Wi-Fi network with a
              short instruction manual. Both examples constitute computer
              networking.
            </h2>
          </Col>
          <Col lg={3}></Col>
          <Col lg={6}>
            <Link to="/contact-us">
              <Button>Enquire Prices</Button>
            </Link>
          </Col>
        </Row>
      </Container>
    </>
  );
}
