import React from "react";
import animatedScrollTo from "animated-scroll-to";
import { Container, Row, Col } from "react-bootstrap";
import { FaRegHdd } from "react-icons/all";
import { isMobile } from "react-device-detect";
import { Button } from "../styles/main-theme";
import { Link } from "react-router-dom";
import Logo from "../addons/images/logo.png";

const MobileView = () => {
  return (
    <Col lg={12}>
      <img src={Logo} alt="logo.png" style={{ width: "100%" }} />
    </Col>
  );
};

export default function DataRecovery() {
  React.useEffect(() => {
    animatedScrollTo(0);
  });

  return (
    <>
      <Container
        style={{
          margin: "10% auto",
          textAlign: "center",
          alignItems: "center",
          justifyItems: "center",
          alignContent: "center",
          justifyContent: "center",
        }}
      >
        <Row>
          {isMobile ? <MobileView /> : null}
          <Col lg={12} style={{ margin: "5% auto" }}>
            <FaRegHdd style={{ transform: "scale(3)" }} />
          </Col>
          <Col lg={12}>
            <u>Data Recovery</u>
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            What is Data Recovery?
            <h2>
              Data recovery is the process of restoring data that has been lost,
              accidentally deleted, corrupted or made inaccessible.
            </h2>
          </Col>
          <Col lg={12}>
            Causes of data loss
            <h2>
              Most data loss is caused by human error, rather than malicious
              attacks, according to U.K. statistics released in 2016. In fact,
              human error accounted for almost two-thirds of the incidents
              reported to the U.K. Information Commissioner's Office. The most
              common type of breach occurred when someone sent data to the wrong
              person
            </h2>
            <br />
            <h2>
              Other common causes of data loss include power outages, natural
              disasters, equipment failures or malfunctions, accidental deletion
              of data, unintentionally formatting a hard drive, damaged hard
              drive read/write heads, software crashes, logical errors, firmware
              corruption, continued use of a computer after signs of failure,
              physical damage to hard drives, laptop theft, and spilling coffee
              or water on a computer.
            </h2>
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            So how does data recovery work?
            <h2>
              The data recovery process varies, depending on the circumstances
              of the data loss, the data recovery software used to create the
              backup and the backup target media. For example, many desktop and
              laptop backup software platforms allow users to restore lost files
              themselves, while restoration of a corrupted database from a tape
              backup is a more complicated process that requires IT
              intervention. Data recovery services can also be used to retrieve
              files that were not backed up and accidentally deleted from a
              computer's file system, but still remain on the hard disk in
              fragments.
            </h2>
            <br />
            <h2>
              Data recovery is possible because a file and the information about
              that file are stored in different places. For example, the Windows
              operating system uses a file allocation table to track which files
              are on the hard drive and where they are stored. The allocation
              table is like a book's table of contents, while the actual files
              on the hard drive are like the pages in the book.
            </h2>
            <br />
            <h2>
              When data needs to be recovered, it's usually only the file
              allocation table that's not working properly. The actual file to
              be recovered may still be on the hard drive in flawless condition.
              If the file still exists -- and it is not damaged or encrypted --
              it can be recovered. If the file is damaged, missing or encrypted,
              there are other ways of recovering it. If the file is physically
              damaged, it can still be reconstructed. Many applications, such as
              Microsoft Office, put uniform headers at the beginning of files to
              designate that they belong to that application. Some utilities can
              be used to reconstruct the file headers manually, so at least some
              of the file can be recovered.
            </h2>
            <br />
            <h2>
              Most data recovery processes combine technologies, so
              organizations aren't solely recovering data by tape. Recovering
              core applications and data from tape takes time, and you may need
              to access your data immediately after a disaster. There are also
              risks involved with transporting tapes.
            </h2>
            <br />
            <h2>
              In addition, not all production data at a remote location may be
              needed to resume operations. Therefore, it's wise to identify what
              can be left behind and what data must be recovered.
            </h2>
          </Col>
          <Col lg={3}></Col>
          <Col lg={6}>
            <Link to="/contact-us">
              <Button>Enquire Prices</Button>
            </Link>
          </Col>
          <Col lg={3}></Col>
        </Row>
      </Container>
    </>
  );
}
