import React from "react";
import { isMobile } from "react-device-detect";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
//paths for destop
import DesktopNav from "./desktop components/DesktopNav";
import DesktopFooter from "./desktop components/DesktopFooter";
import DesktopHome from "./desktop components/DesktopLanding";
import DesktopOtherServices from "./desktop components/DesktopServices";
import DesktopAbout from "./desktop components/DesktopAbout";
import DesktopContact from "./desktop components/DesktopContact";
import DesktopPersonal from "./desktop components/packages/DesktopPersonalPack";
import DesktopSmallBusiness from "./desktop components/packages/DesktopSmallBusiness";
import DesktopLargeBusiness from "./desktop components/packages/DesktopLargeBusiness";

//services imported as Desktop and Mobile
import Pentesting from "./services/pentesting";
import DataRecovery from "./services/dataRecovery";
import Networking from "./services/networking";
import CCTV from "./services/cctv";
import Biometrics from "./services/biometrics";
import MediaMarketing from "./services/mediaMarketing";

//paths for mobile
import MobileNav from "./mobile components/MobileNav";
import MobileFooter from "./mobile components/MobileFooter";
import MobileLanding from "./mobile components/MobileLanding";
import MobileServices from "./mobile components/MobileServices";
import MobileAbout from "./mobile components/MobileAbout";
import MobileContact from "./mobile components/MobileContact";
import MobilePersonalPack from "./mobile components/packages/MobilePersonalPack";
import MobileSmallBusinessPack from "./mobile components/packages/SmallBusinessPackage";
import MobileLargeBusinessPack from "./mobile components/packages/MobileLargeBusiness";

function App() {
  if (!isMobile) {
    return (
      <div className="App">
        <Router>
          <DesktopNav />
          <Switch>
            <Route path="/" exact component={DesktopHome} />
            <Route path="/other-services" component={DesktopOtherServices} />
            <Route path="/about-us" component={DesktopAbout} />
            <Route path="/contact-us" component={DesktopContact} />
            <Route path="/personal-package" component={DesktopPersonal} />
            <Route
              path="/small-business-package"
              component={DesktopSmallBusiness}
            />
            <Route
              path="/large-business-package"
              component={DesktopLargeBusiness}
            />
            <Route path="/penetration-testing" component={Pentesting} />
            <Route path="/data-revocery" component={DataRecovery} />
            <Route path="/networking" component={Networking} />
            <Route path="/cctv" component={CCTV} />
            <Route path="/Biometrics" component={Biometrics} />
            <Route path="/media-marketing" component={MediaMarketing} />
          </Switch>
          <DesktopFooter />
        </Router>
      </div>
    );
  }
  return (
    <div className="App">
      <Router>
        <MobileNav />
        <Switch>
          <Route path="/" exact component={MobileLanding} />
          <Route path="/other-services" component={MobileServices} />
          <Route path="/about-us" component={MobileAbout} />
          <Route path="/contact-us" component={MobileContact} />
          <Route path="/personal-package" component={MobilePersonalPack} />
          <Route
            path="/small-business-package"
            component={MobileSmallBusinessPack}
          />
          <Route
            path="/large-business-package"
            component={MobileLargeBusinessPack}
          />
          <Route path="/penetration-testing" component={Pentesting} />
          <Route path="/data-revocery" component={DataRecovery} />
          <Route path="/networking" component={Networking} />
          <Route path="/cctv" component={CCTV} />
          <Route path="/Biometrics" component={Biometrics} />
          <Route path="/media-marketing" component={MediaMarketing} />
        </Switch>
        <MobileFooter />
      </Router>
    </div>
  );
}

export default App;
