import React from "react";
import animatedScrollTo from "animated-scroll-to";
import { isMobile } from "react-device-detect";
import { Container, Row, Col } from "react-bootstrap";
import { FaFingerprint } from "react-icons/all";
import Logo from "../addons/images/logo.png";
import { Link } from "react-router-dom";
import { Button } from "../styles/main-theme";

const RenderLogo = () => {
  return (
    <Col lg={12}>
      <img src={Logo} alt="logo.png" style={{ width: "100%" }} />
    </Col>
  );
};

export default function Biometrics() {
  React.useEffect(() => {
    animatedScrollTo(0);
  }, []);

  return (
    <>
      <Container style={{ textAlign: "center", margin: "10% auto" }}>
        <Row>
          {isMobile ? <RenderLogo /> : null}
          <Col lg={12}>
            <FaFingerprint
              style={{ transform: "scale(2)", margin: "5% auto" }}
            />
          </Col>
          <Col lg={12}>
            <u>Biometrics</u>
          </Col>
        </Row>
        <Row>
          <Col lg={12} style={{ margin: "5% auto" }}>
            <h2>
              Biometrics are physical or behavioral human characteristics to
              that can be used to digitally identify a person to grant access to
              systems, devices or data.
            </h2>
          </Col>
          <Col lg={12}>
            <h2>
              Examples of these biometric identifiers are fingerprints, facial
              patterns, voice or typing cadence. Each of these identifiers is
              considered unique to the individual, and they may be used in
              combination to ensure greater accuracy of identification.
            </h2>
          </Col>
          <Col lg={12} style={{ margin: "5% auto" }}>
            <h2>
              Because biometrics can provide a reasonable level of confidence in
              authenticating a person with less friction for the user, it has
              the potential to dramatically improve enterprise security.
              Computers and devices can unlock automatically when they detect
              the fingerprints of an approved user. Server room doors can swing
              open when they recognize the faces of trusted system
              administrators. Help desk systems might automatically pull up all
              relevant information when they recognize an employee's voice on
              the support line.
            </h2>
          </Col>
          <Col lg={3} />
          <Col lg={6}>
            <Link to="/contact-us">
              <Button>Enquire Prices</Button>
            </Link>
          </Col>
        </Row>
      </Container>
    </>
  );
}
